footer{
    background-color: var(--color-bg-variant);
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 5rem;
    /* margin-bottom: 3rem; */
}
footer a{
    color: var(--color-bg);
}
.footer-logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}
.permalinks{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}
.footer-socials{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 2rem;
}
.footer-socials a{
    color: aliceblue;
    background-color: var(--color-bg);
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid;
}
.footer-socials a:hover{
    background: transparent;
    color: var(--color-bg-variant);
    color: var(--color-bg);
}
.footer-copyright{
    margin-bottom: 4rem;
    color: var(--color-bg);
}
.footer-copyright small{
    color: var(--color-bg);
}